import React, {useState} from 'react';
import {Image, Platform} from 'react-native';

const RemasteredImage = (props) => {
  //把所有属性赋值给新对象，因为原对象有些属性只读，不能改
  var newProps = new Object();
  for (var key in props) {
    var value = props[key];
    newProps[`${key}`] = value;
  }

  var realSource = props.source;
  // console.log(realSource);
  if (Platform.OS === 'web') {
    //web界面某些界面用路径取不出图片，只能通过default获取data用于赋值
    for (var key in realSource) {
      if (realSource.hasOwnProperty(key)) {
        if (realSource[key].includes('data:image')) {
          realSource = realSource[key];
          newProps.source.uri = realSource;
        }
      }
    }
  }
  //其他情况照旧
  return React.createElement(Image, newProps, props.children);
};

export {RemasteredImage};
