import React, {useState} from 'react';
import {
  SafeAreaView,
  StyleSheet,
  ScrollView,
  Platform,
  View,
  Text,
  StatusBar,
  Image,
  TextInput,
  FlatList,
  SectionList,
  Button,
  Dimensions,
  ImageBackground,
} from 'react-native';
import TestView, {BView} from './test';
import {createStackNavigator} from '@react-navigation/stack';
import {NavigationContainer} from '@react-navigation/native';
import {createBottomTabNavigator} from '@react-navigation/bottom-tabs';
import {RemasteredImage} from './views/RemasteredViews';
import Carousel, {PaginationLight} from 'react-native-x-carousel';
import {fetching} from './common/Fetch';
const {height, width} = Dimensions.get('window');
const Stack = createStackNavigator();
const Tab = createBottomTabNavigator();

const App = () => {
  return (
    <>
      <SafeAreaView style={styles.safeViewStyle}>
        <NavigationContainer>
          <TapNavigator />
        </NavigationContainer>
      </SafeAreaView>
    </>
  );
};

const StackNavigator = () => {
  return (
    <Stack.Navigator>
      <Stack.Screen
        name="Home"
        component={Home}
        options={{
          title: 'mainPage',
          headerTitleAlign: 'center',
          headerRight: () => <Button title="登录"></Button>,
          headerLeft: () => (
            <View
              title="登录"
              style={{
                width: 50,
                height: 50,
                backgroundColor: 'blue',
              }}></View>
          ),
        }}
      />
    </Stack.Navigator>
  );
};

const Home = () => {
  return (
    <>
      <HeaderView>
        <SearchView />
        <BannerView />
      </HeaderView>
      <BottomView>
        <LeftTitlesView />
        <RightContentsView />
      </BottomView>
    </>
  );
};

const TapNavigator = () => {
  return (
    <Tab.Navigator
      screenOptions={({route}) => ({
        tabBarIcon: ({focused, color, size}) => {
          var iconName;

          if (route.name === 'Home') {
            iconName = focused
              ? require('./assets/home_page.png')
              : require('./assets/home_page.png');
          } else if (route.name === 'test') {
            iconName = focused
              ? require('./assets/headers.png')
              : require('./assets/headers.png');
          }
          return (
            <RemasteredImage
              source={iconName}
              style={{
                width: 20,
                height: 20,
                resizeMode: 'cover',
              }}
            />
          );
        },
      })}
      tabBarOptions={{
        activeTintColor: 'tomato',
        inactiveTintColor: 'gray',
      }}>
      <Tab.Screen name="Home" component={StackNavigator} />
      <Tab.Screen name="test" component={BView} />
    </Tab.Navigator>
  );
};

const SearchView = () => {
  return (
    <TextInput
      defaultValue=""
      placeholder="请输入需要搜索的文字"
      style={styles.searchViewStyle}></TextInput>
  );
};

const BannerView = (props) => {
  const showText = [
    {
      url:
        'https://user-images.githubusercontent.com/6414178/73920321-2357b680-4900-11ea-89d5-2e8cbecec9f6.jpg',
    },
    {
      url:
        'https://user-images.githubusercontent.com/6414178/73920358-336f9600-4900-11ea-8eec-cc919b991e90.jpg',
    },
    {
      url:
        'https://user-images.githubusercontent.com/6414178/73927874-25744200-490d-11ea-940f-db3e5dbd8b2b.jpg',
    },
    {
      url:
        'https://user-images.githubusercontent.com/6414178/73920399-45e9cf80-4900-11ea-9d5b-743fe5e8b9a4.jpg',
    },
  ];
  const imageWidth = width;
  const imageHeight = width * 0.57;
  return (
    <>
      <View style={styles.scrollViewStyle}>
        <Carousel
          pagination={PaginationLight}
          data={showText}
          renderItem={(data) => (
            <Image
              source={{uri: data.url}}
              style={{
                width: imageWidth,
                height: imageHeight,
                resizeMode: 'cover',
              }}
              key={data.url}
            />
          )}
          style={{width: imageWidth, height: imageHeight}}
          loop={true}
          autoplay={true}>
          {props.children}
        </Carousel>
      </View>
    </>
  );
};

const HeaderView = (props) => {
  return <View style={styles.headerStyel}>{props.children}</View>;
};

const BottomView = (props) => {
  return <View style={styles.bottomStyle}>{props.children}</View>;
};

const LeftTitlesView = (props) => {
  return <View style={styles.leftTitlesViewStyle}>{props.children}</View>;
};

const RightContentsView = () => {
  return <View style={styles.rightContentsViewStyle}></View>;
};
const styles = StyleSheet.create({
  safeViewStyle: {
    height: Platform.OS === 'web' ? height : '100%',
    flex: 1,
  },
  headerStyel: {
    flex: 2,
    backgroundColor: 'pink',
  },
  searchViewStyle: {
    flex: 1,
    margin: 1,
    backgroundColor: 'white',
    borderColor: 'blue',
  },
  scrollViewStyle: {
    backgroundColor: 'gray',
  },
  bottomStyle: {
    flex: 3,
    flexDirection: 'row',
    backgroundColor: 'skyblue',
  },
  leftTitlesViewStyle: {
    flex: 1,
    marginLeft: 10,
    marginTop: 10,
    marginBottom: 10,
    padding: 10,
    backgroundColor: 'white',
  },
  rightContentsViewStyle: {
    flex: 4,
    marginLeft: 10,
    marginTop: 10,
    marginRight: 10,
    marginBottom: 10,
    backgroundColor: 'cyan',
  },
});

export default App;
